<template>
	<figure class="image">
		<transition :enter-active-class="computedAppearClass">
			<img v-show="loaded" />
		</transition>
		<div v-if="!loaded" class="skeleton">
			<span class="icon">
				<i class="lni lni-reload"></i>
			</span>
		</div>
	</figure>
</template>

<script>
export default {
	name: "BaseImage",
	props: {
		lazyLoad: {
			type: Boolean,
			default: true
		},
		path: {
			type: String,
			required: true
		},
		appearClass: {
			type: String
		}
	},
	data() {
		return {
			loaded: false
		}
	},
	computed: {
		src() {
			return require("@/assets/" + this.path)
		},
		computedAppearClass() {
			if (!this.appearClass) return ""
			return `animate__animated animate__${this.appearClass}`
		}
	},
	mounted() {
		this.img = this.$el.querySelector("img")
		this.skeleton = this.$el.querySelector(".skeleton")
		this.skeleton.style.height = getComputedStyle(this.$el).height
		this.skeleton.style.width = getComputedStyle(this.$el).width
		this.img.onload = () => (this.loaded = true)
		if (!this.lazyLoad) return this.setSrc()
		let observer = new IntersectionObserver(
			entries => {
				console.log(entries[0].intersectionRatio)
				if (entries[0].intersectionRatio > 0.0) {
					this.setSrc()
					observer.unobserve(this.img)
				}
			},
			{
				threshold: 0.2
			}
		)
		observer.observe(this.$el)
	},
	methods: {
		setSrc() {
			this.img.src = this.src
		}
	}
}
</script>

<style lang="scss" scoped>
@keyframes spin {
	from {
		transform: rotate(360deg);
	}
	to {
		transform: rotate(0deg);
	}
}
.skeleton {
	background-color: var(--background-primary-color);
	position: absolute;
	top: 0;
	left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
	i {
    font-size: 2em;
		animation-name: spin;
		animation-duration: 1000ms;
		animation-iteration-count: infinite;
		animation-timing-function: linear;
	}
}
figure.image {
	position: relative;
	border-radius: 10px;
	overflow: hidden;
}
</style>